.home {
	.hero {
		position: relative;
		min-height: 100vh;
		height: 100vh;
		margin: 0;
		padding: 0;

		// background-image: url("/wp-content/themes/wadamfarms/assets/images/hero-bg.jpg");
		background-position: 70%;
		background-size: cover;

		@include breakpoint(mobile) {
			background-position: initial;
		}

		video {
			width: 100vw;
			height: 100vh;
			object-fit: cover;
			// object-position: center;
			// object-position: right;
			object-position: 75% top;
		}

		div {
			position: absolute;
		}

		&__logo {
			top: 60px;
			left: 100px;

			@include breakpoint(mddesktop) {
				left: 20px;
			}

			@include breakpoint(mobile) {
				top: 36px;
				left: 20px;
			}

			img {
				max-width: 330px;

				@include breakpoint(mobile) {
					max-width: 195px;
				}
			}
		}

		&__sidebar {
			top: 60px;
			right: 35px;

			@include breakpoint(mddesktop) {
				right: 20px;
			}

			@include breakpoint(mobile) {
				top: 36px;
				right: 20px;
			}
		}

		&__register {
			right: 0;
			bottom: 0;
			width: 336px;
			height: 100px;
			background-color: map-get($colors, "dark");
			display: flex;
			align-items: center;
			justify-content: center;

			@include breakpoint(mobile) {
				width: 230px;
				height: 70px;
			}

			a {
				font-family: map-get($fonts, 'bold');
				height: 100%;
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				gap: 20px;
				color: map-get($colors, "white");
				font-size: 15px;
				line-height: 18px;
				letter-spacing: 0.4em;

				@include breakpoint(mobile) {
					font-size: 12px;
					line-height: 15px;
					gap: 12px;
				}
			}

			img {
				max-width: 35px;

				@include breakpoint(mobile) {
					max-width: 22px;
				}
			}

			.bounce {
				-moz-animation: bounce 3s infinite;
				-webkit-animation: bounce 3s infinite;
				animation: bounce 3s infinite;
			}
		}

		&__content {
			padding-left: 100px;
			bottom: 40px;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			gap: 20px;

			@include breakpoint(mddesktop) {
				padding-left: 20px;
				bottom: 120px;
			}

			img {
				max-width: 280px;

				@include breakpoint(mobile) {
					max-width: 125px;
				}
			}

			&--tagline {
				width: 100%;
				bottom: 240px;

				@include breakpoint(mddesktop) {
					padding-left: 20px;
					bottom: 240px;
				}

				@include breakpoint(mobile) {
					padding-left: 20px;
					bottom: 220px;
				}
			}
		}

		&__animation {
			opacity: 0;
			width: auto;
			transition: all 0.4s;

			&--fadeInUp {
				animation-name: fadeInUp;
				animation-duration: 500ms;
				animation-fill-mode: both;
			}
		}

		#raw {
			//   margin-top: 200px;
		}

		#wild {}

		#sooke {}

		#tagline {
			max-width: 680px;

			@include breakpoint(mobile) {
				max-width: 324px;
			}

			p {
				font-family: map-get($fonts, 'bold');
				color: map-get($colors, "white");
				font-size: 58.767px;
				line-height: 55px;
				letter-spacing: 0.01em;

				@include breakpoint(mobile) {
					font-size: 30px;
					line-height: 30px;
				}

				span {
					font-family: map-get($fonts, 'mainfont-light');
				}
			}
		}
	}

	.intro {
		padding-top: 192px;
		padding-bottom: 160px;
		position: relative;

		&__lines {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;


			--w: 1px; // Thiclness of the line
			--n: 10; // number of lines

			background: linear-gradient(90deg, #0000 calc(100% - var(--w)), #EFEFEF 0);
			background-size: calc((100% - var(--n)*var(--w))/(var(--n) + 1) + var(--w)) 100%;

			@include breakpoint(tablet) {
				--n: 8;
			}

			@include breakpoint(mobile) {
				--n: 6;
			}
		}

		@include breakpoint(mobile) {
			padding-top: 80px;
			padding-bottom: 80px;
		}

		&__wrapper {
			position: relative;
			z-index: 99;
			// top: 50%;
			// transform: translateX(-50%);
		}

		&__title {
			max-width: 1040px;
			margin: 0 auto;

			h2 {
				font-family: map-get($fonts, 'mainfont-light');
				font-size: 79.7px;
				line-height: 75px;
				text-align: center;
				letter-spacing: 0.01em;

				@include breakpoint(mobile) {
					font-size: 36px;
					line-height: 40px;
				}

				strong {
					font-family: map-get($fonts, 'bold');
				}

				span {
					@include breakpoint(mobile) {
						display: block;
						margin-top: 8px;
					}
				}
			}
		}

		&__content {
			max-width: 952px;
			margin: 0 auto;
			text-align: center;

			p {
				font-family: map-get($fonts, 'mainfont');
				font-size: 23px;
				line-height: 51px;
				letter-spacing: 0.01em;
				margin-top: 64px;
				margin-bottom: 90px;

				@include breakpoint(mobile) {
					font-size: 16px;
					line-height: 32px;
					margin-top: 32px;
					margin-bottom: 48px;
				}

				strong {
					font-family: map-get($fonts, 'bold');
				}

				span {
					@include breakpoint(mobile) {
						display: block;
					}
				}
			}

			img {
				display: block;
				margin: 0 auto;

				@include breakpoint(mobile) {
					max-width: 198px;
				}
			}
		}
	}

}

@keyframes fadeInUp {
	from {
		transform: translate3d(0, 100px, 0);
	}

	to {
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}

@-webkit-keyframes fadeInUp {
	from {
		transform: translate3d(0, 40px, 0);
	}

	to {
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}


@-moz-keyframes bounce {

	0%,
	20%,
	50%,
	80%,
	100% {
		-moz-transform: translateY(0);
		transform: translateY(0);
	}

	40% {
		-moz-transform: translateY(-15px);
		transform: translateY(-15px);
	}

	60% {
		-moz-transform: translateY(-10px);
		transform: translateY(-10px);
	}
}

@-webkit-keyframes bounce {

	0%,
	20%,
	50%,
	80%,
	100% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
	}

	40% {
		-webkit-transform: translateY(-15px);
		transform: translateY(-15px);
	}

	60% {
		-webkit-transform: translateY(-10px);
		transform: translateY(-10px);
	}
}

@keyframes bounce {

	0%,
	20%,
	50%,
	80%,
	100% {
		-moz-transform: translateY(0);
		-ms-transform: translateY(0);
		-webkit-transform: translateY(0);
		transform: translateY(0);
	}

	40% {
		-moz-transform: translateY(-15px);
		-ms-transform: translateY(-15px);
		-webkit-transform: translateY(-15px);
		transform: translateY(-15px);
	}

	60% {
		-moz-transform: translateY(-10px);
		-ms-transform: translateY(-10px);
		-webkit-transform: translateY(-10px);
		transform: translateY(-10px);
	}
}