#register {
    background: map-get($colors, 'gray-light');
    color: map-get($colors, 'gray-dark');

    input,
    select {
        width: 100%;
        background: transparent;
        font-family: map-get($fonts, 'mainfont');
        font-size: 16px;
        line-height: 32px;
        letter-spacing: 1px;

        &:focus-visible {
            outline: none;
        }
    }

    select,
    option {
        font-family: map-get($fonts, 'mainfont');
        font-size: 16px;
        line-height: 32px;
        letter-spacing: 1px;
        color: map-get($colors, 'gray-dark');
        opacity: 1;
    }

    select option:first-child {
        opacity: 0.5;
    }

    ::placeholder,
    .placeholder {
        font-family: map-get($fonts, 'mainfont');
        font-size: 16px;
        line-height: 32px;
        letter-spacing: 1px;
        color: map-get($colors, 'gray-dark');
        mix-blend-mode: normal;
        opacity: 0.5;
    }

    .register {
        &__wrapper {
            max-width: 972px;
            margin: 0 auto;
            padding: 0 20px;
            padding-top: 80px;
            padding-bottom: 120px;

            @include breakpoint(mobile) {
                padding-top: 64px;
                padding-bottom: 64px;
            }
        }

        &__disclaimer {
            .disclaimer {
                font-size: 11px;
                line-height: 13px;
                margin-top: 24px;
                margin-bottom: 48px;

                @include breakpoint(mobile) {
                    font-size: 9px;
                    line-height: 13px;
                    margin-top: 24px;
                    margin-bottom: 36px;
                }
            }
        }

        &__thank-you {
            scroll-margin-top: 120px;
            text-align: center;
            margin-top: 120px;
            margin-bottom: 120px;

            @include breakpoint(mobile) {
                scroll-margin-top: 80px;
                margin-top: 80px;
                margin-bottom: 80px;
            }

            .section__content {
                h2 {
                    font-family: map-get($fonts, 'bold');
                    font-size: 79.6988px;
                    line-height: 75px;
                    letter-spacing: 0.01em;
                    text-transform: uppercase;
                    margin-bottom: 84px;

                    @include breakpoint(mobile) {
                        font-size: 36px;
                        line-height: 40px;
                        margin-bottom: 48px;
                    }
                }

                p {
                    font-size: 23px;
                    line-height: 51px;
                    letter-spacing: 0.01em;
                    margin-bottom: 84px;

                    @include breakpoint(mobile) {
                        font-size: 16px;
                        line-height: 32px;
                        margin-bottom: 48px;
                    }
                }

                .btn-corporate-site {
                    background-color: map-get($colors, "dark");
                    display: inline-block;

                    a {
                        font-family: map-get($fonts, 'bold');
                        display: block;
                        padding: 16px 32px;
                        color: map-get($colors, "white");
                        text-transform: uppercase;
                        font-size: 14.1874px;
                        line-height: 17px;
                        letter-spacing: 0.4em;

                        @include breakpoint(mobile) {
                            font-size: 12.19px;
                            line-height: 15px;
                            letter-spacing: 0.3em;
                        }
                    }
                }
            }
        }
    }

    .section__title {
        margin-bottom: 32px;

        h2 {
            font-family: map-get($fonts, 'bold');
            font-size: 46.2px;
            line-height: 55px;
            letter-spacing: 0.02em;
            text-transform: uppercase;

            @include breakpoint(mobile) {
                font-size: 30px;
                line-height: 36px;
                text-align: center;
            }
        }
    }

    form {

        .form-section {
            @include flex-row(space-between, flex-start);
            flex-wrap: nowrap;

            .form-comments {
                @include flex-col(flex-start, flex-start);
            }

            &.force-row {
                justify-content: unset;
                grid-gap: 8px;
            }
        }

        .form-item {
            margin-bottom: 32px;
            width: calc(50% - 25px);

            @include breakpoint(800px) {
                width: 100%;
            }

            &__radio {
                width: calc(50% - 25px);

                @include breakpoint(800px) {
                    width: 100%;
                }

                label {
                    padding-right: 32px;
                    margin-bottom: 5px;
                    color: map-get($colors, 'gray-dark');
                }

                input {
                    color: map-get($colors, 'gray-dark');
                }

                .radio-wrapper {
                    margin-top: 12px;
                }
            }

            &__text-area {
                width: 100%;
                margin-top: 5px;

                textarea {
                    width: 100%;
                    padding: 10px;
                    margin: 5px 0;
                    color: map-get($colors, 'gray-dark');
                    background: rgba(255, 255, 255, 0.4);
                    border: 0.5px solid map-get($colors, 'gray-dark');
                    box-sizing: border-box;

                    font-family: map-get($fonts, 'mainfont');
                    font-size: 16px;
                    line-height: 32px;
                    letter-spacing: 1px;

                    &:focus-visible {
                        outline: none;
                    }
                }
            }
        }

        input[type=radio],
        input[type=checkbox] {
            margin-right: 8px;
            cursor: pointer;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            outline: 0;
            background: transparent;
            height: 17px;
            width: 17px;
            border: 0.5px solid map-get($colors, 'gray-dark');
            transform: translateY(2px);

            &:checked {
                background: map-get($colors, 'black');
            }
        }

        input[type=checkbox] {
            margin-right: 5px;
        }

        @include breakpoint(800px) {
            .form-section {
                flex-direction: column;
            }

            .force-row {
                flex-direction: row !important;
            }
        }


        .button.submit {
            font-size: 14.19px;
            line-height: 17px;
            letter-spacing: 0.4em;
            text-transform: uppercase;
            margin: 0;

            @include breakpoint(mobile) {
                font-size: 12.19px;
                line-height: 15px;
                margin: 0;
            }
        }
    }
}