/**
*
* 		Footer
*     
*     1. Desktop
*     2. Random Breakpoints
*     3. Tablet
*     4. Mobile
*
**/

footer#footer {
	position: relative;
	z-index: 1;
	padding-top: 64px;
	padding-bottom: 64px;
	background-color: map-get($colors, "dark");

	.footer {
		&__container {
			position: relative;
			z-index: 1;
			display: flex;
			flex-direction: column;
			grid-gap: 96px;

			@include breakpoint(tablet) {
				grid-gap: 56px;
			}
		}

		&__content {
			position: relative;
			color: map-get($colors, "white");
			display: flex;
			justify-content: space-between;

			&--bottom {
				grid-gap: 96px;
				flex-direction: row;

				@include breakpoint(tablet) {
					flex-direction: column;
					grid-gap: 24px;
				}
			}
		}

		&__info {
			@include flex-row(space-between, flex-start);
			grid-gap: 72px;

			@include breakpoint(tablet) {
				grid-gap: 32px;
			}

			p {
				color: map-get($colors, "white");
			}

			&--title {
				font-family: map-get($fonts, 'bold');
				color: map-get($colors, "brand");
				text-transform: uppercase;
				font-size: 30px;
				line-height: 36px;

				@include breakpoint(tablet) {
					font-size: 26px;
					line-height: 31px;
				}
			}

			&--content {
				font-family: map-get($fonts, 'mainfont-light');
				font-size: 30px;
				line-height: 36px;

				@include breakpoint(tablet) {
					font-size: 26px;
					line-height: 31px;
				}
			}

			&--column {
				a {
					color: map-get($colors, "white");
					font-family: map-get($fonts, 'mainfont-light');
				}
			}

			&--address {
				@include flex-row(space-between, flex-start);
				gap: 20px;

				p {
					margin-right: 30px;

					a {
						color: map-get($colors, "white");
						text-decoration: none;

						&:hover {
							color: map-get($colors, "brand");
						}
					}
				}
			}

			&--disclaimer {
				p {
					color: map-get($colors, "white");
					font-size: 11px;
					line-height: 14px;

					@include breakpoint(mobile) {
						font-size: 9px;
						line-height: 11px;
						letter-spacing: -0.01em;
					}
				}
			}

			&--logo {
				min-width: 250px;

				@include breakpoint(tablet) {
					order: -1;
					min-width: 175px;
				}
			}
		}

		&__logo {
			width: 70px;

			@include breakpoint(tablet) {
				width: 40px;
				position: absolute;
				right: 0;
			}
		}

		&__nav {
			&--wrapper {
				@include flex-row(flex-start, flex-start);
				margin: 87px 0;
			}

			&--container {
				margin-top: 73px;

				li {
					a {
						color: map-get($colors, "white");
						font-size: 13px;
						line-height: 24px;
						letter-spacing: 1.25px;
						text-transform: uppercase;
						transition: all 0.3s ease-in-out;

						&:hover {
							color: map-get($colors, "brand");
						}
					}
				}
			}
		}

		&__copyright {
			@include flex-row(flex-start, center);
			margin-top: 73px;
			font-size: 15px;
			line-height: 24px;

			p {
				font-style: normal;
				font-weight: normal;
				font-size: 12px;
				line-height: 18px;
				letter-spacing: 0.03em;
				color: map-get($colors, "white");
			}
		}
	}
}